.images-exposition-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-left: 20vw;
  margin-right: 20vw;
  margin-top: 5vh;
}

.image-exposition {
  width: 80vw;
  padding-bottom: 10px;
}

.paint__information-container {
  margin-top: 0%;
  margin-bottom: 16%;
}

.paint__number,.paint__title,.paint__dimensions,.paint__date,.paint__price {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #596A62;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
}

.paint__dimensions {
  padding-bottom: 20px;
}

.title-exposition {
  padding: 0px 0px 30px 20px;
  margin-top: 54px;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 500;
  font-size: 22px;
  color: #4A4A4A;
}

.title-list {
  padding: 0% 0% 14% 5%;
  margin-top: 16%;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 500;
  font-size: 22px;
  color: #4A4A4A;
}

@media (min-width: 319px) and (max-width: 374px) {
  .paint__number,.paint__title,.paint__dimensions,.paint__date,.paint__price {
    font-size: 10px;
  }

  .title-exposition {
    font-size: 17px;
  }

  .title-list {
    font-size: 15px;
  }
}

@media (min-width: 375px) and (max-width: 481px) {
  .paint__number,.paint__title,.paint__dimensions,.paint__date,.paint__price {
    font-size: 12px;
  }

  .title-exposition {
    font-size: 19px;
  }

  .title-list {
    font-size: 17px;
  }
}

@media (min-width: 481px) and (max-width: 768px) {
  .paint__number,.paint__title,.paint__dimensions,.paint__date,.paint__price {
    font-size: 16px;
  }

  .title-exposition {
    font-size: 30px;
  }

  .title-list {
    font-size: 26px;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  .paint__number,.paint__title,.paint__dimensions,.paint__date,.paint__price {
    font-size: 18px;
  }

  .title-list {
    font-size: 30px;
  }
}

@media (min-width: 769px) and (max-width: 1023px) {
  .title-exposition {
    font-size: 40px;
  }
}

@media (min-width: 1023px) {
  .image-exposition {
    padding-top: 50px;
    padding-bottom: 30px;
  }
}

@media (min-width: 1024px) {
  .title-exposition {
    padding: 30px 0px 40px 50px;
    font-size: 60px;
  }
}

@media (min-width: 1025px) {
  .paint__number,.paint__title,.paint__dimensions,.paint__date,.paint__price {
    font-size: 22px;
  }

  .title-list {
    font-size: 34px;
  }
}
